<template>
	<div class="el-main">


		<el-form ref="applyEcperRef" :inline="true">
			<el-form-item label="">
				<el-switch v-model="auto" active-text="" inactive-text="" ></el-switch>
			</el-form-item>
			<el-form-item label="" class="inlineform">
				<el-select v-model="type" size="small">
					<el-option v-for="item in types" :key="item.value" :label="item.lable" :value="item.value"></el-option>
				</el-select>
			</el-form-item>
			<el-form-item label="" class="inlineform">
				<el-input v-model="coures" size="small" placeholder=""></el-input>
			</el-form-item>
			<el-form-item>
				<el-button type="primary" size="small"></el-button>
			</el-form-item>
		</el-form>


		<el-table :data="dataList" style="width: 100%;" :border="false">
			<el-table-column prop="type" label="">
				<template slot-scope="scope">
					<span v-if="scope.row.type=='insert'"></span>
					<span v-if="scope.row.type=='update'"></span>
					<span v-if="scope.row.type=='delete'"></span>
				</template>
			</el-table-column>
			<el-table-column prop="course" label=""></el-table-column>
			<el-table-column label="">
				<template slot-scope="scope">
					<p v-if="scope.row.type!='update'">{{scope.row.details}}</p>
					<el-popover trigger="click" placement="top" v-if="scope.row.type=='update'">
						<div slot="reference" class="name-wrapper">
							<el-tag size="medium"></el-tag>
						</div>
						<el-table :data="scope.row.details">
							<el-table-column label="" align="center">
								<el-table-column prop="now.name" label=""></el-table-column>
								<el-table-column prop="now.room" label=""></el-table-column>
								<el-table-column prop="now.weak" label=""></el-table-column>
								<el-table-column prop="now.weakday" label=""></el-table-column>
								<el-table-column prop="now.number" label=""></el-table-column>

							</el-table-column>
							<el-table-column label="" align="center">
								<el-table-column prop="now.name" label=""></el-table-column>
								<el-table-column prop="now.room" label=""></el-table-column>
								<el-table-column prop="now.weak" label=""></el-table-column>
								<el-table-column prop="now.weakday" label=""></el-table-column>
								<el-table-column prop="now.number" label=""></el-table-column>
							</el-table-column>
							<el-table-column  prop="details" label=""></el-table-column>
						</el-table>
					</el-popover>
				</template>
			</el-table-column>
			<el-table-column prop="status" label="" align="center"></el-table-column>
			<el-table-column label="" align="center" width="200">
				<template slot-scope="scope">
					<el-button size="small" type="warning" @click="pass(scope.row.id)"></el-button>
					<el-button size="small" type="danger" @click="refuse(scope.row.id)"></el-button>
				</template>
			</el-table-column>
		</el-table>
	</div>

</template>

<script>
	export default {
		name: 'courseAdmin',
		components: {

		},
		methods: {

		},
		data() {
			return {
				dataList: [{
					type: 'insert',
					course: 'zhang',
					details: '',
					status: '',
				}, {
					type: 'update',
					course: 'zhang',
					details: [{
						now: {
							room: 'xxx'
						},
						after: {
							room: 'yyyy'
						},
					}],
					status: '',
				}],
				auto:true,
				type:'',
				coures:'',
				types:[{lable:'',value:'insert'},
				{lable:'',value:'update'},
				{lable:'',value:'delete'},
				]
				

			}
		}
	}
</script>

<style>
.inlineform{
	margin-left: 100px;
}
</style>
