<template>
  <div>
    <el-form ref="applyCourseRef" :inline="true">
      <el-form-item label="">
        <el-input v-model="activityName" size="small" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" size="small"></el-button>
      </el-form-item>
    </el-form>

    <el-table :data="dataList" style="width: 100%;" :border="false">

      <el-table-column prop="date" label=""></el-table-column>
      <el-table-column prop="activity.activityName" label=""></el-table-column>
      <el-table-column prop="activity.date" label=""></el-table-column>
      <el-table-column prop="activity.number" label=""></el-table-column>
      <el-table-column prop="activity.details" label=""></el-table-column>
      <el-table-column prop="activity.classroom.number" label=""></el-table-column>
      <el-table-column prop="activity.people" label=""></el-table-column>
      <el-table-column prop="activity.club.nickname" label=""></el-table-column>
      <el-table-column prop="activity.club.leader" label=""></el-table-column>
      <el-table-column prop="activity.club.tel" label=""></el-table-column>
      <el-table-column label="" align="center" width="150">
        <template slot-scope="scope" v-if="scope.row.status == 0">
          <el-button size="small" type="danger" @click="pass(scope.row.id)"></el-button>
          <el-button size="small" type="danger" @click="review(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>

  </div>

</template>

<script>
export default {
  name: 'courseAdmin',
  components: {},
  methods: {
    review(id){

      this.$prompt('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
      }).then(({ value }) => {
        this.$axios.get("/apply/teacher/unpass?id="+id+'&reason='+value).then(res=>{
          this.$message(res.data.message);
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        });
      });


    },
    pass(id){
      this.$axios.get("/apply/teacher/pass?id="+id).then(res=>{
        this.$message(res.data.message);
      })
    },


    loadList() {
      this.$axios.get("/apply/teacher/club/applies?tid=" + JSON.parse(sessionStorage.getItem("user")).id).then(res => {
        this.dataList = res.data.data;
      })
    },
  },
  mounted() {
    this.loadList()
  }

  ,
  data() {
    return {
      dataList: [{}, {
        type: 'teacher',
        status: '',
      }],
      auto: false,
      type: '',
      activityName: '',
    }
  }
}
</script>

<style>
.headButton {
  margin: 10px auto;
}


</style>


