<template>
  <div class="el-main">
    <el-form ref="applyCourseRef" :inline="true">
      <el-form-item label="" >
        <el-input v-model="activityName" size="small" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" size="small"></el-button>
      </el-form-item>
    </el-form>

    <el-table :data="dataList" style="width: 100%;" :border="false">

      <el-table-column prop="date" label=""></el-table-column>
      <el-table-column prop="activity.activityName" label=""></el-table-column>
      <el-table-column prop="activity.date" label=""></el-table-column>
      <el-table-column prop="activity.number" label=""></el-table-column>
      <el-table-column prop="activity.details" label=""></el-table-column>
      <el-table-column prop="activity.classroom.number" label=""></el-table-column>
      <el-table-column prop="activity.people" label=""></el-table-column>
      <el-table-column prop="status" label="" align="center">
        <template slot-scope="scope">
          <el-button type="text" v-if="scope.row.status == 0"></el-button>
          <el-button type="text" v-if="scope.row.status == 1"></el-button>
          <el-button type="text" v-if="scope.row.status == 2"></el-button>
          <el-button type="text" v-if="scope.row.status == 3">({{scope.row.reason}})</el-button>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" width="150">
        <template slot-scope="scope">

          <el-button size="small" v-if="scope.row.status == 2 || scope.row.status == 3" type="danger" @click="cancel(scope.row.id)"></el-button>
        </template>
      </el-table-column>
    </el-table>

  </div>

</template>

<script>
export default {
  name: 'courseAdmin',
  components: {

  },
  methods: {
    cancel(id){
      this.$axios.get("/apply/del?id="+id).then(res=>{
        this.$message(res.data.message)
      })
    },
    loadList(){
      this.$axios.get("/apply/club/applies?cid="+JSON.parse(sessionStorage.getItem("user")).id).then(res=>{
        this.dataList = res.data.data;
      })
    },
  },
  mounted() {
    this.loadList()
  }

  ,
  data() {
    return {
      dataList: [{}, {
        type: 'teacher',
        status: '',
      }],
      auto: false,
      type: '',
      activityName: '',
    }
  }
}
</script>

<style>
.headButton {
  margin: 10px auto;
}


</style>
