<template>
  <div class="el-main">
    <div class="headButton">
      <el-button @click="addRoom" type="primary" size="small"></el-button>

    </div>
    <div>
      <el-table :data="dataList" style="width: 100%;" :border="false">

        <el-table-column prop="roomName" label=""></el-table-column>
        <el-table-column prop="number" label=""></el-table-column>
        <el-table-column prop="desc" label=""></el-table-column>
        <el-table-column prop="people" label=""></el-table-column>
        <el-table-column prop="teacher.nickname" label=""></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-popover placement="bottom" trigger="click">
              <div slot="reference">
                <el-tag>{{ scope.row.count }}</el-tag>
              </div>
              <el-table :data="scope.row.experiments">
                <el-table-column prop="experimentName" label=""></el-table-column>
                <el-table-column prop="experimentClass" label=""></el-table-column>
                <el-table-column prop="experimentTeacher" label=""></el-table-column>
                <el-table-column prop="experimentPeoples" label=""></el-table-column>
              </el-table>

            </el-popover>
          </template>
        </el-table-column>
        <el-table-column label="" align="center" width="250">
          <template slot-scope="scope">
            <el-button size="small" @click="update(scope.row.id)"></el-button>
            <el-button size="small" type="danger" @click="del(scope.row.id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="el-dialog">
      <add-room-dialog ref="addRoomRef"></add-room-dialog>
      <update-room-dialog ref="updateRoomRef"></update-room-dialog>

    </div>
  </div>

</template>

<script>
import AddRoomDialog from './dialog/AddRoomDialog.vue'
import UpdateRoomDialog from './dialog/UpdateRoomDialog.vue'


export default {
  name: 'experimentAdmin',
  components: {
    AddRoomDialog,
    UpdateRoomDialog,

  },
  methods: {
    loadList() {
      this.$axios.get("/room/rooms").then(res => {
        if (res.data.code == 500) {
          this.$message(res.data.message);
          return
        }
        this.dataList = res.data.data;

      })
    },

    addRoom() {
      this.$refs.addRoomRef.openDialog();

    },

    update(id) {
      this.$refs.updateRoomRef.roomId = id;
      this.$refs.updateRoomRef.openDialog();
    },
    del(id) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(() => {
        this.$axios.get("/room/del?id=" + id).then(res => {
          if (res.data.code == 500) {
            this.$message(res.data.message)
            return
          }
          this.loadList();
        })
          this.$message({
            type: 'success',
            message: '!'
          });
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
      }
    },
    mounted() {
      this.loadList()
    },
    data() {
      return {
        dataList: [{
          roomName: "",
          number: '30903',
          desc: '',
          people: '99',
          count: 0,
          teacher: {id: 0, nickname: 555},
          experiments: [
            {experimentName: 'xxxx', experimentClass: '171', experimentPeoples: 65, experimentTeacher: ''}
          ]

        }],

      }
    }
  }
</script>

<style>
.headButton {
  margin: 10px auto;
}
</style>
