<template>
	<div class="el-main">
		<el-form v-model="form" ref="addNoticeRef">
			<el-form-item label="">
				<el-input v-model="form.title"></el-input>
			</el-form-item>
			
			<el-form-item label="">
				<quill-editor v-model="form.content" :options="editorOption" @blur="onEditorBlur($event)"
					@focus="onEditorFocus($event)" @change="onEditorChange($event)">
				</quill-editor>

			</el-form-item>
			<el-form-item>
				<el-button type="warning" @click="reset"></el-button>
				<el-button type="primary" @click="publish"></el-button>
			</el-form-item>
		</el-form>
	</div>

</template>

<script>
	import {
		quillEditor
	} from 'vue-quill-editor'; //
	import 'quill/dist/quill.core.css'
	import 'quill/dist/quill.snow.css'
	import 'quill/dist/quill.bubble.css'


	export default {
		components: {

			quillEditor,
		},
		methods: {
			onEditorBlur(editor) { // 
			},
			onEditorFocus(editor) { //
			},
			onEditorChange({editor, html, text}) { //
				//this.form.content
				//console.log(this.form.content);
			},
      reset(){
			  this.form.title = '';
			  this.form.content = '';
      },
      publish(){
			  this.$axios.post("/notice/add",this.form).then(res=>{
			    if(res.data.code == 500){
			      this.$message(res.data.message)
            return
          }

			    this.reset();

        })
      }
		},
		computed: {
			editor() {
				return this.$refs.myQuillEditor.quill
			}
		},
		data() {
			return {
			  form:{
          title:'',
          content:'',
        },
				editorOption: {},
			}
		}
	}
</script>

<style>
.quill-editor {
  height: 350px;
  margin: 50px 0;
 }
</style>
